import PropTypes from 'prop-types';
import React, {Component} from 'react';
import {FaBell} from 'react-icons/fa';
import styled from 'styled-components';

const Icon = styled.div.attrs((props) => ({num: props.num}))`
    position: relative;

    &::after {
        content: '${(props) => props.num}';
    }
`;

export default class DashNotice extends Component {
    constructor(props) {
        super(props);
    }

    componentDidUpdate() {
        console.log(this.props);
        for (const notice of this.props.notices) {
            console.log(notice);
        }
    }

    render() {
        const {id, notices, setProps} = this.props;

        return (
            <div id={id} className="dash-notice">
                <Icon num={notices.length > 0 ? notices.length : null}>
                    <FaBell />
                </Icon>
            </div>
        );
    }
}

DashNotice.defaultProps = {
    notices: [],
    remove_type: 'check',
    max_length: 10,
    timeout: '00:30:00',
};

DashNotice.propTypes = {
    id: PropTypes.string,
    notices: PropTypes.arrayOf(PropTypes.object),
    remove_type: PropTypes.string, // check or timeout or length
    max_length: PropTypes.number,
    timeout: PropTypes.string,
    setProps: PropTypes.func,
};
